<template>

    <div class="editor-wrapper">
        <el-container>
            <el-header class="editor-header">
                <router-link to="/dashboard/exerciseSets">
                    返回控制台
                </router-link>

                <el-button @click="createExerciseVisible=true">
                    新建练习
                </el-button>
            </el-header>
            <el-container>
                <el-aside class="editor-aside">
                    <div class="item"
                         :class="{active: editingExercise.id == exercise.id}"
                         @click="()=> edit(exercise.id)"
                         v-for="exercise in exercises">
                        {{exercise.name}}
                    </div>
                </el-aside>
                <el-main >
                    <el-form class="editor-form">
                        <el-form-item>
                            <el-input v-model="editingExercise.name"/>
                        </el-form-item>
                        <el-form-item>
                            <el-input
                                    placeholder="题目问题"
                                    type="textarea"
                                    rows="4"
                                    v-model="editingExercise.question"/>
                        </el-form-item>

                        <el-form-item>
                            <el-input
                                    placeholder="初识化模版"
                                    type="textarea"
                                    rows="4"
                                    v-model="editingExercise.initCode"/>
                        </el-form-item>
                        <el-form-item>
                            <el-input
                                    placeholder="检查代码"
                                    type="textarea"
                                    rows="4"
                                    v-model="editingExercise.checkCode"/>
                        </el-form-item>

                        <el-form-item>
                            <el-select v-model="value" placeholder="请选择日志检测器类型">
                                <el-option value="code" label="代码"/>
                                <el-option value="text" label="文本"/>
                            </el-select>
                        </el-form-item>
                        <el-form-item>

                        </el-form-item>
                        <el-form-item>
                            <el-button
                                    @click="save"
                                    type="primary">保存</el-button>
                        </el-form-item>
                    </el-form>
                </el-main>
            </el-container>
        </el-container>

        <el-dialog :visible.sync="createExerciseVisible">
            <el-form>
                <el-form-item>
                    <el-input v-model="newExerciseName"/>
                </el-form-item>
                <el-form-item style="text-align: right">
                    <el-button @click="handleCreateExercise">创建</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>

<script>
    import {
        createExerciseApi,
        fetchExercisesApi,
        getExerciseConfigApi,
        updateExerciseConfigApi
    } from "../../requests/exercises";

    export default {
        name: "ExerciseSet",
        data() {
            return {
                exercises: [],
                editingExercise: {},
                createExerciseVisible:false,
                newExerciseName:""
            }
        },
        methods: {

            edit(id) {
                getExerciseConfigApi(id).then(response => {
                    this.editingExercise = response.data
                })
            },
            save(){
                updateExerciseConfigApi(this.editingExercise.id , this.editingExercise).then(response=>{
                    this.$message.success("保存工程")
                    this.fetchData()
                })
            },
            fetchData(){
                fetchExercisesApi({
                    sid: this.$route.params.id,
                    all: true
                }).then(response => {
                    this.exercises = response.data.results
                })
            },
            handleCreateExercise(){
                createExerciseApi({
                    name: this.newExerciseName,
                    sid:this.$route.params.id
                }).then(response=>{
                    this.fetchData()
                    this.createExerciseVisible = false
                })
            }
        },
        mounted() {
           this.fetchData()
        }
    }
</script>

<style lang="less" scoped>

    .editor-header {
        height: 60px;
        display: flex;
        position: relative;
        z-index: 10;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        padding: 0 20px;
        box-shadow: 0px 5px 20px 0px rgba(0, 87, 255, 0.1);
    }

    .editor-wrapper {
        height: 100%;
        width: 100%;
        display: flex;
    }

    .editor-form {
        width: 500px;
        margin: 50px auto;
    }

    .editor-aside {
        padding-top: 40px;
        background-color: #545c64;

        .item {
            height: 40px;
            line-height: 40px;
            padding-left: 20px;
            cursor: pointer;
            color: #eee;
        }
        .item.active {
            border-left: 4px solid #fff;
        }
    }
</style>